"use client"

import { useEffect, useState, useRef } from "react"
import { useRouter, useParams } from "next/navigation"
import { Button } from "@/components/ui/button"
import { ChatBubble } from "@/components/chat-bubble"
import { MessageInput } from "@/components/message-input"
import { ConversationList } from "@/components/conversation-list"
import { TypingIndicator } from "@/components/typing-indicator"
import { motion } from "framer-motion"
import { storage } from "@/lib/storage"
import { db } from "@/lib/supabase/db"
import { createClient } from "@/lib/supabase/client"
import type { AuthUser, Conversation, Message } from "@/lib/types"
import { v4 as uuidv4 } from "uuid"
import { ChevronLeft, MoreVertical } from "lucide-react"
import Link from "next/link"
import { Wallpaper } from "@/components/wallpaper"
import { Avatar } from "@/components/avatar"

export default function ChatPage() {
  const router = useRouter()
  const params = useParams()
  const conversationId = params.conversationId as string

  const [auth, setAuth] = useState<AuthUser | null>(null)
  const [conversation, setConversation] = useState<Conversation | null>(null)
  const [conversations, setConversations] = useState<Conversation[]>([])
  const [loading, setLoading] = useState(true)
  const [isTyping, setIsTyping] = useState(false)
  const [otherUserTyping, setOtherUserTyping] = useState(false)
  const [isSending, setIsSending] = useState(false)
  const [connectionStatus, setConnectionStatus] = useState<"connected" | "disconnected" | "connecting">("connecting")
  const messagesEndRef = useRef<HTMLDivElement>(null)
  const typingTimeoutRef = useRef<NodeJS.Timeout | null>(null)
  const otherUserTypingTimeoutRef = useRef<NodeJS.Timeout | null>(null)
  const typingBroadcastThrottleRef = useRef<NodeJS.Timeout | null>(null)
  const channelRef = useRef<ReturnType<typeof supabaseRef.current.channel> | null>(null)
  const supabaseRef = useRef(createClient())
  const heartbeatIntervalRef = useRef<NodeJS.Timeout | null>(null)
  const reconnectTimerRef = useRef<NodeJS.Timeout | null>(null)
  const reconnectAttemptsRef = useRef(0)
  const maxReconnectAttempts = 5

  useEffect(() => {
    const loadData = async () => {
      const authUser = await storage.getAuth()
      if (!authUser) {
        router.push("/login")
        return
      }
      setAuth(authUser)

      const conv = await storage.getConversationById(conversationId)
      if (!conv) {
        router.push("/dashboard")
        return
      }

      setConversation(conv)
      const convs = await storage.getConversationsByBusinessId(authUser.id)
      setConversations(convs)
      setLoading(false)
    }
    loadData()
  }, [conversationId, router])

  // Set up real-time WebSocket subscription for new messages
  const setupRealtimeSubscription = (): ReturnType<typeof supabaseRef.current.channel> | null => {
    if (!conversationId || !auth) return null

    const channelName = `conversation:${conversationId}`
    
    // Clean up existing channel if any
    const existingChannel = supabaseRef.current.getChannels().find((ch) => ch.topic === channelName)
    if (existingChannel) {
      supabaseRef.current.removeChannel(existingChannel)
    }

    const channel = supabaseRef.current
      .channel(channelName, {
        config: {
          broadcast: { self: true },
          presence: { key: auth.id },
        },
      })
      .on("broadcast", { event: "typing" }, (payload) => {
        // Only show typing indicator if it's from the customer
        if (payload.payload.senderType === "customer") {
          setOtherUserTyping(true)
          // Clear typing indicator after 3 seconds
          if (otherUserTypingTimeoutRef.current) {
            clearTimeout(otherUserTypingTimeoutRef.current)
          }
          otherUserTypingTimeoutRef.current = setTimeout(() => {
            setOtherUserTyping(false)
          }, 3000)
        }
      })
      .on(
        "postgres_changes",
        {
          event: "*", // Listen to all events (INSERT, UPDATE, DELETE)
          schema: "public",
          table: "messages",
          filter: `conversation_id=eq.${conversationId}`,
        },
        async (payload) => {
          console.log("Real-time event received:", payload.eventType, payload.new)

          if (payload.eventType === "INSERT" || payload.eventType === "UPDATE") {
            try {
              const dbMessage = payload.new as any
              const newMessage: Message = {
                id: dbMessage.id,
                conversationId: dbMessage.conversation_id,
                senderType: dbMessage.sender_type,
                senderId: dbMessage.sender_id,
                text: dbMessage.content || undefined,
                imageUrl: dbMessage.image_url || undefined,
                status: (dbMessage.status || "sent") as "sent" | "delivered" | "read",
                createdAt: dbMessage.created_at,
              }

              // If it's a new message from the customer, mark it as delivered
              if (payload.eventType === "INSERT" && newMessage.senderType === "customer") {
                // Mark as delivered immediately when received
                db.updateMessageStatus(newMessage.id, "delivered")
              }

              // Update conversation state
              setConversation((prev) => {
                if (!prev) return prev
                
                // Check if message already exists
                const existingIndex = prev.messages.findIndex((m) => m.id === newMessage.id)
                
                // If it's a new message from the customer, clear typing indicator
                if (existingIndex < 0 && newMessage.senderType === "customer") {
                  setOtherUserTyping(false)
                  if (otherUserTypingTimeoutRef.current) {
                    clearTimeout(otherUserTypingTimeoutRef.current)
                  }
                }
                
                if (existingIndex >= 0) {
                  // Update existing message
                  const updated = [...prev.messages]
                  updated[existingIndex] = newMessage
                  return {
                    ...prev,
                    messages: updated.sort(
                      (a, b) => new Date(a.createdAt).getTime() - new Date(b.createdAt).getTime()
                    ),
                    lastMessageAt: newMessage.createdAt,
                  }
                } else {
                  // Add new message
                  return {
                    ...prev,
                    messages: [...prev.messages, newMessage].sort(
                      (a, b) => new Date(a.createdAt).getTime() - new Date(b.createdAt).getTime()
                    ),
                    lastMessageAt: newMessage.createdAt,
                  }
                }
              })

              // Update conversations list
              const currentAuth = await storage.getAuth()
              if (currentAuth) {
                const convs = await storage.getConversationsByBusinessId(currentAuth.id)
                setConversations(convs)
              }
            } catch (error) {
              console.error("Error processing real-time message:", error)
            }
          } else if (payload.eventType === "DELETE") {
            // Remove deleted message
            const deletedId = payload.old?.id
            if (deletedId) {
              setConversation((prev) => {
                if (!prev) return prev
                return {
                  ...prev,
                  messages: prev.messages.filter((m) => m.id !== deletedId),
                }
              })
            }
          }
        }
      )
      .on(
        "postgres_changes",
        {
          event: "UPDATE",
          schema: "public",
          table: "conversations",
          filter: `id=eq.${conversationId}`,
        },
        async (payload) => {
          console.log("Conversation updated via WebSocket:", payload)
          const updated = await storage.getConversationById(conversationId)
          if (updated) {
            setConversation(updated)
          }
        }
      )
      .subscribe((status, err) => {
        console.log("Subscription status:", status)

        if (status === "SUBSCRIBED") {
          console.log(" Successfully subscribed to messages via WebSocket")
          setConnectionStatus("connected")
          reconnectAttemptsRef.current = 0 // Reset on successful connection
          channelRef.current = channel // Store channel reference for broadcasting

          // Set up heartbeat to keep connection alive
          if (heartbeatIntervalRef.current) {
            clearInterval(heartbeatIntervalRef.current)
          }
          heartbeatIntervalRef.current = setInterval(() => {
            channel.send({
              type: "presence",
              event: "heartbeat",
              payload: { user_id: auth.id, timestamp: Date.now() },
            })
          }, 30000) // Send heartbeat every 30 seconds
        } else if (status === "CHANNEL_ERROR" || status === "TIMED_OUT" || status === "CLOSED") {
          setConnectionStatus("disconnected")
          console.error("WebSocket connection error:", status, err)

          // Clear heartbeat
          if (heartbeatIntervalRef.current) {
            clearInterval(heartbeatIntervalRef.current)
            heartbeatIntervalRef.current = null
          }

          // Attempt reconnection with exponential backoff
          if (reconnectAttemptsRef.current < maxReconnectAttempts) {
            reconnectAttemptsRef.current++
            const delay = Math.min(1000 * Math.pow(2, reconnectAttemptsRef.current), 30000) // Max 30 seconds

            console.log(
              `Reconnecting in ${delay}ms (attempt ${reconnectAttemptsRef.current}/${maxReconnectAttempts})...`
            )

            reconnectTimerRef.current = setTimeout(() => {
              setupRealtimeSubscription()
            }, delay)
          } else {
            console.error("Max reconnection attempts reached. Falling back to polling.")
            setConnectionStatus("disconnected")
          }
        }
      })

    return channel
  }

  useEffect(() => {
    if (!conversationId || !auth) return

    const channel = setupRealtimeSubscription()
    if (!channel) return

    return () => {
      console.log("Cleaning up WebSocket subscription")
      if (heartbeatIntervalRef.current) {
        clearInterval(heartbeatIntervalRef.current)
      }
      if (reconnectTimerRef.current) {
        clearTimeout(reconnectTimerRef.current)
      }
      if (otherUserTypingTimeoutRef.current) {
        clearTimeout(otherUserTypingTimeoutRef.current)
      }
      channelRef.current = null
      supabaseRef.current.removeChannel(channel)
    }
  }, [conversationId, auth?.id])

  // Fallback: Poll for new messages if WebSocket fails (every 5 seconds)
  const lastMessageCountRef = useRef(0)
  useEffect(() => {
    if (!conversation?.id || connectionStatus === "connected") {
      lastMessageCountRef.current = conversation?.messages.length || 0
      return
    }

    // Only poll if not connected
    const pollInterval = setInterval(async () => {
      try {
        const updated = await storage.getConversationById(conversation.id)
        if (updated && updated.messages.length > lastMessageCountRef.current) {
          // New messages detected via polling
          setConversation(updated)
          lastMessageCountRef.current = updated.messages.length

          // Update conversations list
          const currentAuth = await storage.getAuth()
          if (currentAuth) {
            const convs = await storage.getConversationsByBusinessId(currentAuth.id)
            setConversations(convs)
          }
        }
      } catch (error) {
        console.error("Error polling for messages:", error)
      }
    }, 5000) // Poll every 5 seconds when disconnected

    return () => {
      clearInterval(pollInterval)
    }
  }, [conversation?.id, connectionStatus])

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" })
  }, [conversation?.messages])

  // Mark messages as read when conversation is viewed
  useEffect(() => {
    if (!conversationId || !auth) return

    // Mark all customer messages as read when business views the conversation
    const markAsRead = async () => {
      try {
        await db.markMessagesAsRead(conversationId, "customer")
        // Refresh conversation to update status
        const updated = await storage.getConversationById(conversationId)
        if (updated) {
          setConversation(updated)
        }
      } catch (error) {
        console.error("Error marking messages as read:", error)
      }
    }

    // Mark as read after a short delay to ensure messages are loaded
    const timeout = setTimeout(markAsRead, 500)
    return () => clearTimeout(timeout)
  }, [conversationId, auth?.id])

  // Cleanup typing timeout on unmount
  useEffect(() => {
    return () => {
      if (typingTimeoutRef.current) {
        clearTimeout(typingTimeoutRef.current)
      }
      if (otherUserTypingTimeoutRef.current) {
        clearTimeout(otherUserTypingTimeoutRef.current)
      }
      if (typingBroadcastThrottleRef.current) {
        clearTimeout(typingBroadcastThrottleRef.current)
      }
    }
  }, [])

  const handleSendMessage = async (text: string) => {
    if (!conversation || !auth) return
    if (isSending) return // Prevent duplicate sends

    setIsSending(true)

    const tempId = uuidv4()
    const newMessage: Message = {
      id: tempId,
      conversationId: conversation.id,
      senderType: "business",
      senderId: auth.id,
      text,
      status: "sent",
      createdAt: new Date().toISOString(),
    }

    // Optimistically update UI immediately
    const optimisticConversation: Conversation = {
      ...conversation,
      messages: [...conversation.messages, newMessage],
      lastMessageAt: new Date().toISOString(),
    }
    setConversation(optimisticConversation)
    setIsTyping(false)

    try {
      // Send to database - real-time subscription will handle the update
      await db.createMessage(newMessage)
      
      // Update conversations list after a short delay to allow real-time to update
      setTimeout(async () => {
        const convs = await storage.getConversationsByBusinessId(auth.id)
        setConversations(convs)
      }, 500)
    } catch (error) {
      console.error("Failed to send message:", error)
      // Revert optimistic update on error
      setConversation(conversation)
    } finally {
      setIsSending(false)
    }
  }

  const handleSendImage = async (imageUrl: string) => {
    if (!conversation || !auth) return
    if (isSending) return

    setIsSending(true)

    const tempId = uuidv4()
    const newMessage: Message = {
      id: tempId,
      conversationId: conversation.id,
      senderType: "business",
      senderId: auth.id,
      imageUrl,
      status: "sent",
      createdAt: new Date().toISOString(),
    }

    // Optimistically update UI immediately
    const optimisticConversation: Conversation = {
      ...conversation,
      messages: [...conversation.messages, newMessage],
      lastMessageAt: new Date().toISOString(),
    }
    setConversation(optimisticConversation)
    setIsTyping(false)

    try {
      // Send to database - real-time subscription will handle the update
      await db.createMessage(newMessage)
      
      // Update conversations list after a short delay
      setTimeout(async () => {
        const convs = await storage.getConversationsByBusinessId(auth.id)
        setConversations(convs)
      }, 500)
    } catch (error) {
      console.error("Failed to send image:", error)
      setConversation(conversation)
    } finally {
      setIsSending(false)
    }
  }

  const handleTyping = () => {
    setIsTyping(true)
    if (typingTimeoutRef.current) {
      clearTimeout(typingTimeoutRef.current)
    }
    typingTimeoutRef.current = setTimeout(() => {
      setIsTyping(false)
    }, 3000) // Hide typing indicator after 3 seconds of inactivity

    // Broadcast typing status to the other user (throttled to once per second)
    if (channelRef.current && conversationId && !typingBroadcastThrottleRef.current) {
      channelRef.current.send({
        type: "broadcast",
        event: "typing",
        payload: {
          senderType: "business",
          conversationId: conversationId,
        },
      })
      
      // Throttle broadcasts to once per second
      typingBroadcastThrottleRef.current = setTimeout(() => {
        typingBroadcastThrottleRef.current = null
      }, 1000)
    }
  }

  if (!auth || loading || !conversation) return null

  return (
    <main className="h-screen flex flex-col md:flex-row bg-[var(--chat-bg)] dark:bg-[var(--chat-bg)] relative">
      <Wallpaper businessLogo={auth?.business?.businessLogo} />
      {/* Sidebar */}
      <motion.aside
        initial={{ opacity: 0, x: -20 }}
        animate={{ opacity: 1, x: 0 }}
        className="hidden md:flex w-80 border-r border-border flex-col bg-card/80 backdrop-blur-sm z-10"
      >
        <div className="p-4 border-b border-border">
          <Link href="/dashboard">
            <h1 className="text-2xl font-bold text-primary cursor-pointer hover:opacity-80">Leenk</h1>
          </Link>
        </div>
        <div className="flex-1 overflow-y-auto">
          <ConversationList conversations={conversations} selectedId={conversationId} />
        </div>
      </motion.aside>

      {/* Chat Area */}
      <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }} className="flex-1 flex flex-col bg-[var(--chat-bg)] dark:bg-[var(--chat-bg)] relative z-10">
        {/* Chat Header */}
        <div className="border-b border-border bg-card/80 backdrop-blur-sm p-3 md:p-4 flex items-center justify-between">
          <div className="flex items-center gap-3">
            <Link href="/dashboard" className="md:hidden">
              <Button variant="ghost" size="sm">
                <ChevronLeft className="w-4 h-4" />
              </Button>
            </Link>
            <Avatar name={conversation.customerName || conversation.customerEmail} size="md" />
            <div>
              <h2 className="font-semibold text-foreground">{conversation.customerName || conversation.customerEmail}</h2>
              <p className="text-xs text-muted-foreground">
                {conversation.customerEmail}
                {connectionStatus === "connected" && (
                  <span className="ml-2 text-green-500" title="Real-time connected"></span>
                )}
                {connectionStatus === "disconnected" && (
                  <span className="ml-2 text-yellow-500" title="Reconnecting..."></span>
                )}
              </p>
            </div>
          </div>
          <Button variant="ghost" size="sm">
            <MoreVertical className="w-4 h-4" />
          </Button>
        </div>

        {/* Messages */}
        <div className="flex-1 overflow-y-auto p-3 md:p-4 space-y-2 relative z-0">
          {conversation.messages.length === 0 ? (
            <div className="flex items-center justify-center h-full text-muted-foreground">
              <p>No messages yet. Start the conversation!</p>
            </div>
          ) : (
            <>
              {conversation.messages.map((msg, idx) => (
                <ChatBubble key={msg.id} message={msg} isOwn={msg.senderType === "business"} index={idx} />
              ))}
              {otherUserTyping && <TypingIndicator isOwn={false} />}
            </>
          )}
          <div ref={messagesEndRef} />
        </div>

        {/* Message Input */}
        <div className="bg-card/80 backdrop-blur-sm border-t border-border relative z-10">
          <MessageInput 
            onSendMessage={handleSendMessage} 
            onSendImage={handleSendImage} 
            onTyping={handleTyping}
            disabled={isSending}
          />
        </div>
      </motion.div>
    </main>
  )
}
